<template>
  <div id="app" class="row">
    <div class="col-md-offset-6 col-md-6 todo-outer">
      <h1 class="app-name"> todos app</h1>
      <hr>

       <input class="new-todo"
          autofocus autocomplete="off"
          placeholder="What needs to be done?"
          v-model="newTodo"
          @keyup.enter="addTodo" min=3 max=50 required>
          <div>
            {{newTodo}}
          </div>

            <div class="todo-list">
            <div class= "todo-list-item">
               <input type="checkbox" value="111"> Toggle all todo status
          </div>
              <div class= "todo-list-item" v-for="todoItem in todos">
                  <div class="col-md-1"> <input type="checkbox" value="111" v-model="todoItem.isCompleted"></div>
                  <div  class="col-md-8" v-bind:class="{ 'todo-completed': todoItem.isCompleted }"> {{todoItem.id}} - {{todoItem.text}} </div>
                  <div class="col-md-3" > <span class="glyphicon glyphicon-trash text-danger"> </span></div>
                </div>
            </div>
            <hr>
            <div class="todo-footer">
                <div class="col-sm-3"> <span>
                  {{ todos.length}} items
                </span>  </div>
                <div class="col-sm-3">
                    <button class=""  @onclick="removeTodo"> All </button>
                 </div>
                <div class="col-sm-3">  <button class=""  @onclick ="removeTodo()"> Completed </button>  </div>
                <div class="col-sm-3">  <button class="" @onclick="removeTodo()"> Clear all completed </button> </div>
            </div>
      </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      newTodo: '',
      todos: [
        {id: 1, text: 'goto park', isCompleted: true},
        {id: 2, text: 'goto to cinema', isCompleted: true},
        {id: 3, text: 'goto to Hyderabad', isCompleted: false},
        {id: 4, text: 'goto to Goa', isCompleted: false}
      ],
      count_uncompleted: 1
    }
  },

}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.app-name{
  color: pink;
}
.new-todo{
  width:90%;
}

.todo-outer{
  border: 2px solid grey;
  border-radius: 2%;
  margin :  20px 20px 20px 20px ;
  padding : 20px 20px 20px 20px ;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.todo-list {
    text-align: left;
}
.todo-list-item{
    margin :  10px 10px 10px 10px ;
    padding :  15px 15px 15px 15px ;
}
.todo-completed{
  text-decoration: line-through;
}
.todo-footer{
  margin: 10px 10px 10px 10px;
}
</style>

Vue.component('todo-item', {
  template: `
  <li>
  This is a todo
  </li>
  `
})


var app5 = new Vue({
  el: '#app-5',
  data: {
    message: 'Hello Vue.js!'
  },
  methods: {
    reverseMessage: function () {
      this.message = this.message.split('').reverse().join('')
    }
  }
})



